import { Component, createEffect, Show } from "solid-js";
import {
  getSingleWalletModalIsOpened,
  getSingleWalletModalWalletInfo,
} from "../../../state/modals-state";
import { H1Styled, StyledModal } from "./style";
import { useI18n } from "@solid-primitives/i18n";
import { appState } from "../../../../config/app.state";
import { isMobile, updateIsMobile } from "../../../hooks/isMobile";
import {MobileConnectionModal, MobileConnectionProps} from "./mobile-connection-modal";
import { Dynamic } from "solid-js/web";
import {DesktopConnectionModal, DesktopConnectionProps} from "./desktop-connection-modal";
import { WalletsModalCloseReason } from "../../../../models";
import LoadingModalPage from "./layout/LoadingModalPage";
import {EngineConnectInfo} from "@okxconnect/core";

export interface SingleWalletModalProps {
  loadingBeforeConnect: () => boolean;
  universalLinkParams: () => EngineConnectInfo | null
  connect: () => any;
  onClose: (closeReason: WalletsModalCloseReason) => void;
  isSingleWallet: boolean;
}

export const SingleWalletModal: Component<SingleWalletModalProps> = (props) => {
  const { locale } = useI18n()[1];
  createEffect(() => locale(appState.language));

  createEffect(() => {
    if (getSingleWalletModalIsOpened()) {
      updateIsMobile();
    }
  });

  return (
    <StyledModal
      opened={getSingleWalletModalIsOpened()}
      onClose={() => props.onClose("action-cancelled")}
      data-tc-wallets-modal-container="true"
    >
      <Show when={props.loadingBeforeConnect()}>
        <H1Styled>{getSingleWalletModalWalletInfo()?.name}</H1Styled>
        <LoadingModalPage />
      </Show>

      <Show when={!props.loadingBeforeConnect()}>
        <Dynamic
          component={
            isMobile() ? MobileConnectionModal : DesktopConnectionModal
          }
          wallet={getSingleWalletModalWalletInfo()!} // TODO: remove non-null assertion
          onBackClick={() => {}}
          backDisabled={true}
          connect={props.connect}
          isSingleWallet={props.isSingleWallet}
          universalLinkParams = {props.universalLinkParams}
        />
      </Show>
    </StyledModal>
  );
};
